/* Needed to show the "Enter VR" icon */
a-scene {position: absolute; height: 100%; width: 100%}

/*body { width: 100%; height: 100%; margin: 0; padding: 0; }*/

.buttons {
  position: absolute;
  z-index: 1;
  padding: 5px;
  text-align: center;
  margin: auto;
  width: 100%;
}

button {
  margin: 2px;
  padding: 10px;
  border-radius: 4px;
  background-color: lightgrey;
  color: black;
  opacity: 0.5;
  cursor: pointer;
}
.loading {
  position: absolute;
  width: 98%;
  top: 45%;
  text-align: center;
  font-family: sans-serif;
  font-size: large;
  color: cornflowerblue;
}


